import { createSlice } from '@reduxjs/toolkit';
const initialState = [];
const hashtagSlice = createSlice({
    name: 'hashtags',
    initialState,
    reducers: {
        addTag: (state, action) => {
            const tags = action.payload;
            tags.forEach((tag) => {
                if (!state.includes(tag)) {
                    state.push(tag);
                }
            });
        },
    },
});
export const { addTag } = hashtagSlice.actions;
export default hashtagSlice.reducer;
